/* tslint:disable */
import * as React from "react";
import {
  getId,
  PersonaPresence,
  PersonaSize,
  IconButton,
  css,
  ValidationState
} from "office-ui-fabric-react";
import { Persona } from "office-ui-fabric-react/lib/Persona";
import { IPeoplePickerItemProps } from "office-ui-fabric-react/lib/components/pickers/PeoplePicker/PeoplePickerItems/PeoplePickerItem.types";
/* tslint:enable */

import * as stylesImport from "./PickerItemsCus.module.scss";
const styles: any = stylesImport;

export const CusSelectedItem: (props: IPeoplePickerItemProps) => JSX.Element = (
  peoplePickerItemProps: IPeoplePickerItemProps
) => {
  const {
    item,
    onRemoveItem,
    index,
    selected,
    removeButtonAriaLabel
  } = peoplePickerItemProps;
  // console.log(item);
  //item.coinProps=null;
  item.showUnknownPersonaCoin = false;
  const itemId = getId();
  // tslint:disable-next-line:typedef
  const onClickIconButton = (
    removeItem: (() => void) | undefined
  ): (() => void) => {
    return (): void => {
      if (removeItem) {
        removeItem();
      }
    };
  };

  return (
    <div
      className={css(
        "ms-PickerPersona-container",
        styles.personaContainer,
        { ["is-selected " + styles.personaContainerIsSelected]: selected },
        {
          ["is-invalid " + styles.validationError]:
            item.ValidationState === ValidationState.warning
        }
      )}
      data-is-focusable={true}
      data-is-sub-focuszone={true}
      data-selection-index={index}
      role={"listitem"}
      aria-labelledby={"selectedItemPersona-" + itemId}
    >
      <div
        className={css("ms-PickerItem-content", styles.itemContent)}
        id={"selectedItemPersona-" + itemId}
      >
        <Persona
          {...item}
          presence={
            item.presence !== undefined ? item.presence : PersonaPresence.none
          }
          size={PersonaSize.size28}
          /*  onRenderCoin={  (props: IPersonaProps): JSX.Element => {
            console.log("test........");
            return (<img src="http://sznews.com/photo/images/attachement/jpg/site3/20180518/IMG4439c452438747660281629.jpg"/>); }}
            */
        />
      </div>
      <IconButton
        onClick={onClickIconButton(onRemoveItem)}
        iconProps={{ iconName: "Cancel", style: { fontSize: "12px" } }}
        className={css("ms-PickerItem-removeButton", styles.removeButton)}
        ariaLabel={removeButtonAriaLabel}
      />
    </div>
  );
};
